import React, { Component } from 'react';
import GatsbyLink from 'gatsby-link'
import UserLinks from '../UserLinks/UserLinks';

import './Footer.scss';

import {
  Container,
  Row,
  Col,
} from '../mystyle'

class Footer extends Component {
  render() {
    const { config } = this.props;
    const {copyright, phone} = config;
    if (!copyright) {
      return null;
    }
    return (
      <footer className="footer">
        <div className="footer-inner">
          <div className="extra-zone">
            <div className="inner">
              <Container>
                <div className="footer-widgets row">
                  <div className="col-xs-12 col-md-3 footer-widget-1">
                    <div className="widget widget-container">
                      <div className="widget-body">
                        <div className="textwidget">
                          <div className="group-icons">
                            <p className="home-icon">
                              <span className="pr-10">
                                <i className="ion-android-pin"/>
                              </span>
                              {config.address}
                            </p>
                            <p className="phone-icon">
                              <span className="pr-10">
                                <i className="ion-android-call"/>
                              </span>
                              <a href="tel:0988 990 168">
                                {phone}
                              </a>
                            </p>
                            <p className="email-icon">
                              <span className="pr-10">
                                <i className="ion-android-mail"/>
                              </span>
                              <a href={`mailto:${config.email}`} target="_blank">{config.email}</a>
                            </p>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                  <div className="col-xs-12 col-md-3 footer-widget-2">
                    <div className="widget widget-container">
                      <div className="widget-body">
                        <div className="menu">
                          <ul className="menu">
                            <li className="menu-item">
                              <GatsbyLink to="/support-and-faq">Hng dn mua hng</GatsbyLink>
                            </li>
                            <li className="menu-item">
                              <GatsbyLink to="/support-and-faq">Hnh thc thanh ton</GatsbyLink>
                            </li>
                          </ul>
                        </div>
                      </div>
                    </div>
                  </div>
                  <div className="col-xs-12 col-md-3 footer-widget-3">
                    <div className="widget widget-container">
                      <div className="widget-body">
                        <div className="menu">
                          <dl>
                            <dt>Vietcombank</dt>
                            <dd>Lu Bnh An</dd>
                            <dd>0181-003-023-502</dd>
                          </dl>
                          <dl>
                            <dt>ACB</dt>
                            <dd>Trng Th T Mi</dd>
                            <dd>0181-003-023-502</dd>
                          </dl>
                        </div>
                      </div>
                    </div>
                  </div>
                  <div className="col-xs-12 col-md-3 footer-widget-4">
                    <div className="widget widget-container">
                      <div className="widget-body">
                        <div className="menu">
                          <UserLinks config={config} labeled />
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </Container>
            </div>
          </div>
          <Container>
            <Row>
              <Col>
                <div className="copyright">
                  {copyright}
                </div>
              </Col>
              <Col>
                <div className="credit">
                  <ul className="footer-nav">
                    <li>
                      <GatsbyLink to="/support-and-faq">Cu hi thng gp</GatsbyLink>
                    </li>
                  </ul>
                </div>
              </Col>
            </Row>
            
            
          </Container>
        </div>
      </footer>
    );
  }
}

export default Footer;



